<template>
  <div id="app-login">
    <div class="background">
      <img :src="imgSrc" width="100%" height="110%" style="margin-top: -15px" alt=""/>
    </div>


    <el-row>
      <el-col :span="10" :offset="7">
        <div class="login-box">
          <div class="form-aside">
            <a class="logo" href="http://www.baidu.com">
              <img class="logo-image" src="https://static.zhipin.com/v2/web/geek/images/logo.png">
              <div class="logo-text">
                <p></p>
                <p></p>
              </div>
            </a>
            <a class="aside-other">
              <img class="logo-image" src="../../assets/loginImage/loginIcon1.jpg">
              <div class="other-text">
                <p></p>
                <p></p>
              </div>
            </a>
            <a class="aside-other">
              <img class="logo-image" src="../../assets/loginImage/loginIcon2.jpg">
              <div class="other-text">
                <p></p>
                <p></p>
              </div>
            </a>

          </div>

          <div class="form-main">
            <el-form ref="loginForm" :model="form" :rules="rules" v-if="loginRegistReplace" label-width="80px"
                     class="main-box">


              <el-form-item label="" prop="username">
                <el-input type="text" placeholder="" v-model="form.bossAccount"/>
              </el-form-item>
              <el-form-item label="" prop="password">
                <el-input type="password" placeholder="" v-model="form.password"/>
              </el-form-item>
              <el-form-item prop="isverify" style="width: 115%;margin-left: -15%">
                <el-popover
                  placement="right"
                  width="400"
                  heigth="600"
                  trigger="click">
                  <q-slide-verify v-if="!form.isverify" v-model="form.isverify"></q-slide-verify>
                  <el-button type="success" slot="reference" style="width: 100%" :disabled="form.isverify">
                    {{ form.isverify==false?'':'' }}
                  </el-button>
                </el-popover>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" v-on:click="onSubmit('loginForm')"
                           onmouseenter="this.style.background = '#5dd5c8'"
                           onmouseleave="this.style.background = '#00d7c6'"
                           style="width: 120%; margin-left: -20%; background: #00d7c6 ;border: none">
                </el-button>
              </el-form-item>
              <el-form-item>
                <el-button type="text" style="color: #00d7c6"></el-button>
                <el-button type="text" style="color: #00d7c6"></el-button>
                <el-button type="text" style="color: #00d7c6" v-on:click="replace()"></el-button>
              </el-form-item>


            </el-form>

            <el-form ref="registForm" :model="form" :rules="rules" v-if="!loginRegistReplace" label-width="80px"
                     class="main-box">


              <el-form-item label="" prop="username">
                <el-input type="text" placeholder="" v-model="form.username"/>
              </el-form-item>

              <el-form-item prop="isverify" style="width: 115%;margin-left: -15%">
                <!--          <el-input type="text" placeholder="" v-model="form.securityCode" style="width: 70%"></el-input>-->
                <el-popover
                  placement="right"
                  width="400"
                  heigth="600"
                  trigger="click">
                  <q-slide-verify v-if="!form.isverify" v-model="form.isverify"></q-slide-verify>
                  <el-button type="success" slot="reference" style="width: 100%" :disabled="form.isverify">
                    {{ form.isverify==false?'':'' }}
                  </el-button>
                </el-popover>


              </el-form-item>

              <el-form-item>
                <el-button type="primary" v-on:click="onSubmit('registForm')"
                           style="width: 120%; margin-left: -20%; background: #00d7c6 ;border: none">
                </el-button>
              </el-form-item>
              <el-form-item>
                <el-checkbox v-model="registChecked" style="font-size: 5px;margin-left: -45px;"> 
                </el-checkbox>
                <span><el-button type="text" style="color: #00d7c6" v-on:click="replace()"></el-button></span>
              </el-form-item>

            </el-form>

          </div>

          <br clear="both">

        </div>
      </el-col>
    </el-row>


  </div>
</template>

<script>

  import QSlideVerify from "../../components/qSlideVerify";
  import {mapMutations} from 'vuex';

  export default {
    name: "",
    components: {'q-slide-verify': QSlideVerify},

    data() {
      var checkIsverify = (rule, value, callback) => {
        if (value != true) {
          callback(new Error(''));
        } else {
          callback();
        }
      };
      return {
        form: {
          bossAccount: '',
          password: '',
          isverify: false
        },

        //  el-form-item  prop 
        rules: {
          bossAccount: [
            {required: true, message: '', trigger: 'blur'}
          ],
          password: [
            {required: true, message: '', trigger: 'blur'}
          ],
          /*isverify: [
            {validator: checkIsverify, trigger: 'blur'}
          ]*/
        },

        // 
        dialogVisible: false,
        // 
        loginRegistReplace: true,
        // 
        registChecked: false,
        //

        imgSrc: 'https://qiubucket.oss-cn-guangzhou.aliyuncs.com/recruitment_web_static/img/bossLoginPageBackgrond.png'

      }
    },
    methods: {
      //
      onSubmit(formName) {
        // 

        let _this = this;

        this.$refs[formName].validate((valid) => {
          if (valid) {
            _this.$axios({
              method: 'post',
              url: _this.APPConstant.API_HEAD + '/re-company-boss/login',
              data: _this.form,
              headers: {
                "Accept": "application/json",
              }
            }).then(res => {
              // tokenlocalStorage
              localStorage.setItem(_this.APPConstant.ACCESS_BOSS_TOKEN, res.data.data.boss_token)
              localStorage.setItem("bossDetail", JSON.stringify(res.data.data.reCompanyBoss))
              _this.$router.push('/boss/bossMainPage')
            }).catch(error => {
              alert('');
            });
          } else {
            this.dialogVisible = true;
            this.$message({
              message: '',
              type: 'error'
            });
            return false;
          }
        });
      },
      //
      replace() {
        this.loginRegistReplace = !this.loginRegistReplace;
        this.form.isverify = false;
      }
    },
    mounted() {

    }
  }
</script>

<style scoped>

  #app-login {
    overflow: hidden;
  }

  .background {
    width: 100%;
    height: 100%; /**100% */
    z-index: -1;
    position: absolute;
    top: 0px;
  }

  .front {
    z-index: 1;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    /*width: 100px;*/
    /*height: 100px;*/
    /*background: red;*/
  }

  .login-box {
    border: 1px solid #DCDFE6;
    /*width: 800px;
    height: 800px;*/
    margin: 130px auto;
    /*padding: 35px 35px 15px 35px;*/
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    box-shadow: 0 0 25px #909399;
    background: white;
  }

  .main-box {
    margin-top: 10px;
  }

  .form-aside {
    float: left;
    width: 30%;
    height: 400px;
    background: #f8fcff;
  }

  .logo {
    float: none;
    display: block;
    width: 200px;
    height: 40px;
    margin: 88px 0 0 30px;
    padding: 0;
  }

  .logo-image {
    width: 42px;
    height: 42px;
    float: left;
  }

  .logo-text {
    margin-left: 45px;
  }

  .logo-text > p {
    color: #00c2b3;
    line-height: 20px;
    font-size: 14px;
    margin: 0;
  }

  .aside-other {
    float: none;
    display: block;
    width: 200px;
    height: 40px;
    margin: 50px 0 0 28px;
    padding: 0;
  }

  .other-text {
    margin-left: 44px;
  }

  .other-text > p {
    color: #8d92a1;
    line-height: 20px;
    font-size: 13px;
    margin: 0;
  }

  .form-main {
    float: left;
    /*position: relative;
    top: 18%;
    left: 12%;
    width: 45%;*/
    width: 320px;
    padding: 60px 60px 20px 60px;
  }

  .login-title {
    text-align: center;
    margin: 0 auto 40px auto;
    color: #303133;
  }

  /*  ::-webkit-scrollbar {
      width: 0 !important;
    }

    ::-webkit-scrollbar {
      width: 0 !important;
      height: 0;
    }*/
</style>



