<template>
  <el-row id="top">
    <el-col :span="18" :offset="3" class="inner-top">
      <el-col :span="3" class="logo">
        <a href="https://www.zhipin.com/" ka="header-home-logo" title="BOSS"><span></span></a>
      </el-col>
      <el-col :span="3" class="nav-city">
        <i class="el-icon-location-outline"></i>
        <span class="nav-city-selected"></span>
        <span class="switchover-city">[]</span>
      </el-col>
      <el-col :span="10" class="nav">
        <el-menu mode="horizontal"
                 :default-active="selected"
                 background-color="#202329"
                 text-color="#fff"
                 active-text-color="#00d7c6"
                 @select="jumpPage">
          <el-menu-item index="/"></el-menu-item>
          <el-menu-item index="/jobMain"></el-menu-item>
          <el-menu-item index="/companyMain"></el-menu-item>
          <el-menu-item index="4"></el-menu-item>
        </el-menu>
      </el-col>
      <el-col :span="8" class="user-nav">
        <el-menu v-if="name!=null" mode="horizontal"
                 :default-active="selected"
                 @select="jumpPage"
                 background-color="#202329"
                 text-color="#fff"
                 active-text-color="#00d7c6"
        >
          <el-menu-item index="1"></el-menu-item>
          <el-menu-item index="/user/resume"></el-menu-item>
          <el-menu-item index="3"></el-menu-item>
          <el-submenu index="4">
            <template slot="title">{{name}}</template>
            <el-menu-item index="/user/center"></el-menu-item>
            <el-menu-item index="4-2"></el-menu-item>
            <el-menu-item index="4-3"></el-menu-item>
            <el-menu-item index="4-4"></el-menu-item>
            <el-menu-item index="/login"></el-menu-item>
          </el-submenu>
        </el-menu>
        <el-button v-else class="not-login-button"
                   type="success"
                   size="medium"
                   onmouseenter="this.style.background = '#00d7c6' "
                   onmouseleave="this.style.background = '#00c2b3';this.style.color = '#fff' "
                   @click="toLoginPage"
        >
        </el-button>

      </el-col>
      <!--
            <el-col v-if="name == null" :span="8" class="login-nav">
      -->

    </el-col>
  </el-row>
</template>

<script scoped>
  export default {
    name: "maintop",
    props: ["selected"],
    data() {
      return {
        name: null
      };
    },
    methods: {
      toLoginPage() {
        this.$router.push('/login')
      },
      jumpPage(key, keyPath) {
        if (key === '/login') {
          this.$axios.get(this.APPConstant.API_HEAD+'/re-user//logout')
          .then(res => {
            if(res.data.code === 0){
              localStorage.removeItem(this.APPConstant.JWT_ACCESS_TOKEN)
              localStorage.removeItem('userDetail')
            }
          })
        }
        this.$router.push(key) // B
      },
      initData() {
        var userDetail = JSON.parse(localStorage.getItem('userDetail'));
        if (userDetail == null) {
          this.name = null
        } else {
          this.name = userDetail.name;
        }
      }

    },
    mounted() {
      this.initData()
    }
  }
</script>

<style scoped>
  #top {
    background: #202329;
  }

  .inner-top {
    display: flex;
    align-items: center;
  }

  .el-button {

    color: #fff;
    background: #00c2b3;
    border: none;
  }


  #top a {
    color: #fff;
    text-decoration: none;
  }

  .logo span {
    font-family: ;
    font-weight: bold;
    font-size: 20px;
    color: #00d7c6;
  }

  .nav-city {
    color: #fff;
  }
  .nav-city .nav-city-selected {
    color: #00d7c6;
  }
  .el-icon-location-outline{
    color: #00d7c6;
  }

  .user-nav {
    position: relative;
  }

  .not-login-button {
    position: absolute;
    right: 3px;
    bottom: -15px;
  }
</style>



